import { useState, useEffect } from "react";
import fullQuestions from "../data/talentQuestions.json";
import "./TalentTest.css";

// âœ… éšæœºæŠ½é¢˜å‡½æ•°
function getRandomQuestions(count = 10) {
  return fullQuestions.sort(() => 0.5 - Math.random()).slice(0, count);
}

