    "é¢†å¯¼åŠ›":
      "ðŸ”¥ ä½ å¤©ç”Ÿå…·å¤‡å¼ºå¤§çš„é¢†å¯¼æ½œè´¨ï¼Œèƒ½å¤Ÿåœ¨å›¢é˜Ÿä¸­è‡ªç„¶åœ°æ¿€åŠ±ä»–äººã€å‡èšåŠ›é‡ã€‚ä½ çš„å†³æ–­åŠ›ã€è¡ŒåŠ¨åŠ›ä¸Žè´£ä»»æ„Ÿè®©ä½ æˆä¸ºå€¼å¾—ä¿¡èµ–çš„äººã€‚å»ºè®®ä½ ç»§ç»­æå‡æ²Ÿé€šä¸Žæƒ…ç»ªç®¡ç†çš„èƒ½åŠ›ï¼Œå¹³è¡¡â€œæ‰§è¡Œâ€ä¸Žâ€œå€¾å¬â€ã€‚å½“ä½ èƒ½å…¼é¡¾ç»“æžœä¸Žå…³ç³»ï¼Œä½ çš„é¢†å¯¼åŠ›å°†æ›´å…·å½±å“åŠ›ï¼Œæˆä¸ºå¼•å¯¼ä»–äººå®žçŽ°ç›®æ ‡çš„æ ¸å¿ƒäººç‰©ã€‚",
