export default function TalentTest({ onFinish }) {
  const [questions, setQuestions] = useState([]);
  const [step, setStep] = useState(0);
  const [scores, setScores] = useState({});
  const [result, setResult] = useState(null);

  // âœ… åˆå§‹åŒ–æ—¶éšæœºæŠ½10é¢˜
  useEffect(() => {
    const todayKey = new Date().toISOString().split("T")[0];
    const cacheKey = `talentTest_${todayKey}`;
    const cached = localStorage.getItem(cacheKey);
    if (cached) {
      setQuestions(JSON.parse(cached));
    } else {
      const selected = getRandomQuestions(10);
      setQuestions(selected);
      localStorage.setItem(cacheKey, JSON.stringify(selected));
      Object.keys(localStorage)
        .filter((k) => k.startsWith("talentTest_") && k !== cacheKey)
        .forEach((oldKey) => localStorage.removeItem(oldKey));
    }
  }, []);

  const handleAnswer = (type) => {
    setScores((prev) => ({ ...prev, [type]: (prev[type] || 0) + 1 }));
    if (step + 1 < questions.length) {
      setStep(step + 1);
    } else {
      generateResult();
    }
  };

  const generateResult = () => {
    const topType = Object.entries(scores).sort((a, b) => b[1] - a[1])[0]?.[0];
    const description = generateResultDescription(topType);
    setResult({ type: topType, description });
    localStorage.setItem(
      "talentResult",
      JSON.stringify({ type: topType, date: new Date() })
    );
  };

  if (result) {
    return (
      <div className="talent-result">
        <h2>ðŸ§¬ ä½ çš„å¤©èµ‹ç±»åž‹ï¼š{result.type}</h2>
        <p>{result.description}</p>
        <button
          onClick={() =>
            onFinish ? onFinish("home") : (window.location.href = "/")
          }
        >
          è¿”å›žä¸»é¡µ
        </button>
      </div>
    );
  }

  if (questions.length === 0) return <p>é¢˜ç›®åŠ è½½ä¸­...</p>;

  const q = questions[step];

  return (
    <div className="talent-test">
      <h2>ðŸŒŸ è¶…å¼ºå¤©èµ‹æµ‹è¯•</h2>
      <p>{q.question}</p>
      <div className="options">
        {q.options.map((opt, idx) => (
          <button key={idx} onClick={() => handleAnswer(opt.type)}>
            {opt.text}
          </button>
        ))}
      </div>
      <p>è¿›åº¦ï¼š{step + 1} / {questions.length}</p>
    </div>
  );
}
